.r-tab-item {
  flex: none;
  font-size: 0.875rem;
  font-weight: 600;
  border-bottom-width: 2px;
  border-color: transparent;
  height: 2.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-top: -1px;
}

.r-tab-item:focus {
  outline: 0;
  border-color: #0069B7;
}

.r-tab-item:hover {
  border-color: #C6CCD0;
}

.r-tab-item--active {
  border-color: #0076CC;
}

.r-tab-item--disabled {
  color: #9BA3A7;
  pointer-events: none;
}